.container{padding:20px;margin-top:100px;max-width:800px;margin:0 auto;background-color:#ebeff3;border-radius:8px;box-shadow:0 2px 4px rgba(0,0,0,.1);font-family:"DM Sans",sans-serif}.labelHeader{font-size:24px;font-weight:bold;margin-bottom:20px;color:#6734ff;font-family:"Roboto",sans-serif;text-align:center;font-family:"DM Sans",sans-serif}.label{display:block;font-weight:bold;margin-bottom:8px}.inputGroup{margin-bottom:10px;font-family:"Roboto",sans-serif}.fileInput{width:100%;padding:10px;border:1px solid #ccc;border-radius:4px;margin-top:5px;height:50px;font-family:"Roboto",sans-serif}.select{width:100%;height:40px;padding:10px;border:1px solid #ccc;border-radius:4px;margin-top:5px;resize:vertical;box-sizing:border-box;font-family:"Roboto",sans-serif;color:#000;background-color:#fff;text-align:initial}.textarea{width:100%;height:150px;padding:10px;border:1px solid #ccc;border-radius:4px;margin-top:5px;resize:vertical;box-sizing:border-box;font-family:"Roboto",sans-serif}.button{width:100%;margin-top:20px;padding:12px;background-color:#6734ff;color:#fff;font-weight:bold;border:none;border-radius:4px;cursor:pointer;text-align:center;font-family:"Roboto",sans-serif;font-size:15px}.button:hover{background-color:#005bb5}.button:disabled{background-color:#ccc;cursor:not-allowed}.fullscreenSpinner{position:fixed;top:0;left:0;width:100vw;height:100vh;background:rgba(0,0,0,.5);display:flex;flex-direction:column;justify-content:center;align-items:center;z-index:1000;color:#fff}.fullscreenSpinner .spinner{border:4px solid #f3f3f3;border-top:4px solid #0070f3;border-radius:50%;width:40px;height:40px;animation:spin 1s linear infinite}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}.result{margin-top:20px}.result .table{width:100%;border-collapse:collapse}.result .table th,.result .table td{border:1px solid #ddd;padding:8px}.result .table th{background-color:#f4f4f4}.result .totalPoints{margin-top:10px;font-weight:bold}